.section {
    width: 100%;
}

.section__content {
    background-color: #fff;
}

.breadcrumb {
    background-color: #fff;
    border-radius: 0;
}

.breadcrumb-item {
    color: $grey;
    font-size: 15px;
}

.breadcrumb-item.active {
    color: $green;
    text-transform: uppercase;
    a {
        color: $green;
        font-size: 15px;
        &:hover {
            color: $black;
        }
    }
}

.breadcrumb-item + .breadcrumb-item::before {
    content: ">";
    padding: 0 20px;
}

a {
    text-decoration: none;
    transition: .3s;
    &:hover {
        text-decoration: none;
    }
}

.btn {
    cursor: pointer;
    text-align: center;
    background-color: $green;
    border: none;
    color: #fff;
    width: 100%;
    border-radius: 3px;
    font-weight: 600;
    text-transform: uppercase;
    font-size: 14px;
    padding: 15px 60px;
    overflow: hidden;
    position: relative;
    &:before {
        display: block;
        content: '';
        position: absolute;
        width: 60%;
        height: 100%;
        top: 0;
        left: -100%;
        background-color: rgba(255, 255, 255, .3);
        transform: skew(45deg);
        transition: .3s;
    }
    &:after {
        display: block;
        content: '';
        position: absolute;
        width: 60%;
        height: 100%;
        top: 0;
        right: -100%;
        background-color: rgba(255, 255, 255, .3);
        transform: skew(45deg);
        transition: .3s;
    }
    &:hover {
        &:before {
            left: -10%;
        }
        &:after {
            right: -10%;
        }
    }
    &--tizer {
        padding: 15px;
        &:before {
            width: 64%;
        }
        &:after {
            width: 64%;
        }
        &:hover {
            &:before {
                left: -14%;
            }
            &:after {
                right: -14%;
            }
        }
    }
}

.section__title {
    text-align: left;
    width: 100%;
    color: $black;
    font-size: 28px;
}

@keyframes links {
    to {
        left: 0;
        width: 100%;
    }
}